import { ComponentMeta, } from "@plasmicapp/host";

import React, { ReactNode } from "react";

export function ensure<T>(x: T | null | undefined): T {
  if (x === null || x === undefined) {
    debugger;
    throw new Error(`Value must not be undefined or null`);
  } else {
    return x;
  }
}

const modulePath = "@plasmicpkgs/plasmic-revue";

interface RevueSignupFormProps {
  className?: string;
  children?: ReactNode;
  name?: string;
  emailInput?: ReactNode;
  subscribeButton?: ReactNode;
}

export const RevueSignupFormMeta: ComponentMeta<RevueSignupFormProps> = {
  name: "RevueSignupForm",
  displayName: "Revue Signup Form",
  importName: "RevueSignupForm",
  importPath: modulePath,
  providesData: true,
  description: "Shows a sign up form to users for subscribe to newsletter",
  defaultStyles: {
    display: "flex",
    margin: "-4px -5px",
  },
  props: {
    emailInput: {
      type: "slot",
      defaultValue: {
        type: "vbox",
        styles: {
          padding: "0px",
          margin: "4px 5px",
          width: "200%",
        },
        children: {
          type: "component",
          name: "FormInput",
        },
      },
    },
    subscribeButton: {
      type: "slot",
      defaultValue: {
        type: "vbox",
        styles: {
          padding: "0px",
          margin: "4px 5px",
        },
        children: {
          type: "component",
          name: "SubscribeButton",
        },
      },
    },
    name: {
      type: "string",
      displayName: "Account Name",
      description: "Go to your account Revue page and copy your name from top left corner",
      defaultValue: "abdukhamidl"
    },
  },
};

export function RevueSignupForm({
  className,
  subscribeButton,
  emailInput,
  name,
}: RevueSignupFormProps) {

  return (
    <>
      <form
        action={`https://www.getrevue.co/profile/${name}/add_subscriber`}
        method="post"
        id="revue-form"
        name="revue-form"
        target="_blank"
        className={className}
      >
        {emailInput}
        {subscribeButton}
      </form>
    </>
  );
}


interface InputProps extends React.InputHTMLAttributes<HTMLInputElement> { }

export const SubscribeButton = ({ className, ...props }: InputProps) => {
  return (
    <input
      className={className}
      {...props}
      type="submit"
      name="member[subscribe]"
      id="member_submit"
      value="Subscribe"
    />
  );
};

export const SubscribeButtonMeta: ComponentMeta<InputProps> = {
  name: "SubscribeButton",
  displayName: "Subscribe Button",
  importName: "SubscribeButton",
  importPath: modulePath,
  props: {},
  defaultStyles: {
    height: "42px",
    width: "100%",
    fontFamily: "Roboto",
    fontSize: "14px",
    backgroundColor: "#2BBDF5",
    borderWidth: "0",
    color: "#ffffff",
    borderRadius: "4px",
  },
};
export const FormInput = ({ className, ...props }: InputProps) => {
  return (
    <input
      className={className}
      {...props}
      type="email"
      name="member[email]"
      placeholder="Enter your email"
      required
    />
  );
};

